$accent-color: #D58C51;
$bg-color: #161516;
$dark-btn-font-color: #131313;
$dark-form-font-color: #161516;
$agreement-font-color: #301411;

@mixin  mix_button($bg-color: $accent-color, $color: $dark-btn-font-color, $border: 1px solid $accent-color) {
    background: $bg-color;
    width: 200px;
    height: 42px;
    border: $border;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $color;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    cursor: pointer; }
h1 {
    font-weight: 700;
    font-size: 31px;
    line-height: 38px;
    text-transform: uppercase;
    color: #fff;
    text-align: left; }

.cardTitle {
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    color: #fff;
    text-align: left; }
.cardPrice {
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    color: #fff; }

input {
    -webkit-appearance: none;
    appearance: none; }
@media(max-width: 992px) {
    h1 {
        font-size: 28px; } }
@media(max-width: 576px) {
    h1 {
        font-size: 24px; } }
