@import "../../assets/styles";

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #fff;
    width: 460px;
    height: 340px;
    padding: 7px 20px 34px;
    &>a, h1, form, label, div {
        background-color: #fff; }
    .formLink {
        font-weight: 300;
        font-size: 11px;
        line-height: 13px;
        text-decoration-line: underline;
        color: $accent-color;
        background-color: #fff;
        align-self: flex-end;
        cursor: pointer; }
    h1 {
        font-weight: 700;
        font-size: 31px;
        line-height: 38px;
        text-transform: uppercase;
        color: $dark-form-font-color;
        margin: 15px 0; }
    form {
        display: flex;
        flex-direction: column;
        width: 100%;
        input {
            border: 1px solid $accent-color;
            // -webkit-appearance: none
 }            // appearance: none
        input:focus {
            color: red;
            background: #E0EFCA; }

        input[type="text"], input[type="password"] {
            width: 100%;
            height: 39px;
            border-radius: 61px;
            padding: 9px 18px 10px;
            margin: 5px 0;
            font-size: 16px;
            line-height: 20px;
            color: $dark-form-font-color;
            background-color: #fff !important; }
        span {
            font-weight: 300;
            font-size: 8px;
            height: 10px;
            color: #FF0B0B;
            text-align: left;
            background-color: #fff;
            margin-left: 18px; }
        .submitError {
            text-align: center;
            margin-bottom: 12px; }
        label {
            font-weight: 300;
            font-size: 11px;
            line-height: 13px;
            color: $agreement-font-color;
            text-align: left;
            display: flex;
            justify-content: center;
            align-items: center;
            align-self: flex-start;
            margin: 2px 0 6px;
            position: relative;
            padding-left: 27px;
            input[type="radio"] {
                position: absolute;
                top: 1px;
                left: 3px;
                width: 10px;
                height: 10px;
                opacity: 0;
                z-index: 10;
                &:checked + .radioControl {
                    box-shadow: inset 0 0 0 3px #fff;
                    background-color: $accent-color; } }

            .radioControl {
                position: absolute;
                top: -2.5px;
                left: 0;
                width: 18px;
                height: 18px;
                border: 1px solid $accent-color;
                border-radius: 50%;
                color: $accent-color; } }
        button {
            @include  mix_button($bg-color: $accent-color, $color: $dark-btn-font-color, $border: 1px solid $accent-color);
            align-self: center; } } }
@media(max-width: 576px) {
    .form {
        width: 300px; } }
