@import "../../assets/styles";

.cart {
    display: flex;
    align-items: center;
    .cartInfo > p {
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: right;
        color: #fff; }
    .cartSignContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: $accent-color;
        margin: 0 20px;
        &:hover {
            background-color: transparent;
            border: 1px solid $accent-color; }
        &:hover .cartSign {
            background-color: $bg-color; }
        &:hover .cartSign * {
            fill: $accent-color; }

        .cartSign {
            background-color: $accent-color;
            padding-bottom: 4px;
            cursor: pointer; } } }


