@import "../../assets/styles";

.card {
    width: 312px;
    padding: 43px 21px 32px;
    border: 1px solid $accent-color;
    color: #fff;
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: min-content;
    justify-content: center;
    &:hover>p {
        color: $accent-color; }
    &:hover>div>div>p {
        color: $accent-color; }
    .cardTitle {
        margin: 31px 0 1px;
        cursor: pointer; }
    .cardDescr {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-align: left; }
    .cardFooter {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
        margin-top: 41px; //-10 -5 -7
        align-self: flex-end;
        &>div {
            display: flex;
            align-items: baseline;
            .cardPrice {
                margin-right: 4px; }
            .cardOutput {
                font-weight: 400;
                font-size: 14px;
                line-height: 17px; } } } }

@media(max-width: 992px) {
   .card {
        width: 250px;
        .cardImg {
            width: 216px; } } }
@media(max-width: 768px) {
    .card {
        align-items: center;
        .cardImg {
            width: 108px; }
        .cardTitle {
            font-size: 14px;
            font-weight: 400;
            word-wrap: break-word;
            hyphens: auto;
            overflow-wrap: break-word;
            grid-column: 2/3;
            width: 85px;
            margin: 0 0 1px 10px;
            align-self: center; }
        .cardDescr {
            grid-column: 1/3;
            margin-top: 10px;
            font-size: 12px; }
        .cardFooter {
            grid-column: 1/3;
            margin-top: 20px; } } }
