@import "../../assets/styles";

.orderItem {
    display: grid;
    grid-template-columns: 36.8vw 60px 115px; //530px
    column-gap: 7vw; //(262px-60px)/2
    align-items: center;
    margin-bottom: 35px;
    .orderData {
        display: grid;
        grid-template-columns: 132px 21.5vw; //310px
        column-gap: 6.8%; //98px
        align-items: center;
        .orderImg {
            height: 132px;
            background-size: cover;
            background-repeat: no-repeat; }
        .orderTitle {
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            color: #fff;
            text-align: left; } }
    .orderPriceData {
        display: grid;
        grid-template-columns: auto 30px;
        column-gap: 15px;
        align-items: center;
        .orderPrice {
            font-weight: 500;
            font-size: 17px;
            line-height: 21px;
            color: $accent-color; }
        button {
            transform: rotate(90deg);
            color: $accent-color;
            border-color: $accent-color; } } }
@media(max-width: 992px) {
    .orderItem {
        grid-template-columns: 80px 190px;
        grid-auto-rows: min-content;
        column-gap: 70px;
        row-gap: 30px;
        .orderData {
            grid-template-columns: 132px auto; //310px
            grid-column: 1/3;
            grid-row: 1/2;
            justify-self: flex-start; }
        form {
            grid-row: 2/3;
            grid-column: 1/2;
            justify-self: flex-end;
            margin-left: 20px; }

        .orderPriceData {
            grid-row: 2/3;
            grid-column: 2/3;
            justify-self: flex-start; } } }

